.App {
  width: 1280px;
  --header_height: 60px;
  margin: 107px auto 63px;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.hw {
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  padding: 24px 0 32px;
}

.hw > div {
  width: 1140px;
  display: flex;
  flex-direction: column;
  margin: 0 auto 56px;
}

.hwTitle {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #000;
  margin: 0 0 10px;
}
